<template>
    <v-app light>
        <v-toolbar>
          <v-toolbar-side-icon ></v-toolbar-side-icon>
          <v-toolbar-title> Welcome to JS Recorder!
          </v-toolbar-title>
          <a href="#">
            <img v-bind:class="classval" v-bind:src ="srcval" width="40" height="40" v-on:click="enabledisable">
          </a>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialog1" max-width="325px">
              <v-btn  slot='activator'>IMPORT</v-btn>
              <v-card>
                <v-card-text>
                    <input type="file" v-on:change="setselectioninlocal"/>
                </v-card-text>
                <v-card-actions >
                    <v-btn color="blue darken-1"  @click.native="importsteps">Save</v-btn>
                </v-card-actions>
              </v-card>
          </v-dialog>
          <v-btn  @click.native="resetdata">RESET</v-btn>
            <v-toolbar-items>
                <v-btn flat >
                  <v-icon left>supervisor_account</v-icon>
                  github:@nirmchan
                </v-btn>
            </v-toolbar-items>
        </v-toolbar>
        <v-card>
                <v-card-title>
                  <v-btn  @click.native="fetchsteps"> Refresh</v-btn>
                  <v-btn  @click.native="deleteselected">DELETE</v-btn>
                  <v-dialog v-model="dialog" max-width="500px">
                      <v-btn slot='activator'>Export</v-btn>
                      <v-card>
                          <v-card-text>
                            <v-text-field v-model="filename" required label="Enter File name"></v-text-field>
                          </v-card-text>
                          <v-card-actions>
                              <v-btn color="blue darken-1"  @click.native="exportvalues">Save</v-btn>
                          </v-card-actions>
                      </v-card>
                 </v-dialog>
                 <v-dialog v-model="dialog2"  max-width="500px">
                     <v-btn slot='activator'  @click.native="editormode">INSERT</v-btn>
                     <v-card>
                          <v-card-title>
                            <span class="headline">INSERT</span>
                            <v-spacer></v-spacer>
                            <v-text-field label="Rownumber" hint="Row where record to be inserted" required v-model="temprecord.rowindex"></v-text-field>
                          </v-card-title>
                          <v-card-text>
                            <v-container grid-list-md>
                              <v-layout wrap>
                                <v-flex xs12 sm6 md4>
                                  <v-text-field label="TagName" hint="HTML Tag name" v-model="temprecord.TagName"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md4>
                                  <v-text-field label="Pagename" hint="Name of the page if any" v-model="temprecord.Pagename"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md4>
                                  <v-text-field label="Objectname" hint="Optional Identifier" v-model="temprecord.Objectname"
                                    persistent-hint
                                    required
                                  ></v-text-field>
                                </v-flex>
                                <v-flex xs12>
                                  <v-text-field label="RelativeXpath" hint="Relativexpath from Root html" v-model="temprecord.RelativeXpath">
                                  </v-text-field>
                                </v-flex>
                                <v-flex xs12>
                                  <v-text-field label="AbsoluteXpath" hint ="Absolutexpath of the element" required v-model="temprecord.Absolutexpath">
                                  </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6>
                                  <v-select
                                    label="Action"
                                    required
                                    :items="['click', 'Enter', 'Select']"
                                    v-model="temprecord.Selecteditemineditor"
                                  ></v-select>
                                </v-flex>
                                <v-flex xs12 sm6>
                                  <v-text-field label="ValueIfAny" hint ="Required for Action Type Enter and Select" persistent-hint v-model="temprecord.ValueIfAny" >
                                  </v-text-field>
                                </v-flex>
                                <v-flex xs12>
                                  <v-text-field label="Urlval" hint ="Required item for intelligent playback" persistent-hint v-model="temprecord.Url"></v-text-field>
                                </v-flex>
                              </v-layout>
                            </v-container>
                            <small>*indicates required field</small>
                          </v-card-text>
                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn @click.native="dialog2 = false">Close</v-btn>
                            <v-btn color="blue darken-1"  @click.native="insertrecord">Save</v-btn>
                          </v-card-actions>
                        </v-card>
                </v-dialog>
                  <v-spacer></v-spacer>
                  <v-text-field append-icon="search" label="Search" single-line  hide-details  v-model="search"></v-text-field>
                </v-card-title>
              <v-data-table  hide-actions v-bind:headers="headers"  :items="computeditems"
                  class="elevation-1 " v-model="selected" item-key="index" select-all   v-bind:search="search">
                        <template slot="items" slot-scope="props">
                              <td>
                                <v-checkbox  primary  hide-details v-model="props.selected"></v-checkbox>
                              </td>
                              <td style="max-width:20%">{{ props.index+1}}</td>
                              <td style="max-width:20%" >{{ props.item.TagName }}</td>
                              <td style="max-width:20%">{{ props.item.pagename }}</td>
                              <td style="max-width:20%">{{ props.item.ObjectName }}</td>
                              <td style="max-width:20%"><input v-model="props.item.RelativeXpath"></td>
                              <!-- <td style="max-width:20%">{{props.item.CssSelector}}</td> -->
                              <td style="max-width:20%" >{{ props.item.Action }}</td>
                              <td style="max-width:20%" ><input v-model="props.item.ValueIfAny"></td>
                              <td style="max-width:20%" >{{ props.item.IsRequired }}</td>
                              <td style="max-width:20%"><input v-model="props.item.Url"></td>
                              <td style="max-width:20%">
                                  <select><option v-for="abspath in props.item.AbsoluteXpath">{{abspath}}</option></select>
                              </td>
                  </template>
               </v-data-table>
               <!-- <div class="text-xs-center pt-2">
                    <v-pagination v-model="pagination.page" :length="pages"></v-pagination>
              </div> -->
            </v-card>
    </v-app>
</template>

<script>
  export default {
    data :function () {
      return {
        headers: [
         {text: 'Index #',align:'center',value: 'Index'},
         { text: 'TagName',align:'center', value: 'TagName' ,sortable: false},
         { text: 'PageName',align:'center', value: 'PageName',sortable: false },
         { text: 'ObjectName', align:'center',value: 'ObjectName',sortable: false},
         { text: 'RelativeXpath', align:'center',value: 'RelativeXpath',sortable: false },
         // { text: 'CssSelector',align:'center', value: 'CssSelector',sortable: false },
         { text: 'Action', align:'center',value: 'Action',sortable: false },
         { text: 'ValueIfAny',align:'center', value: 'ValueIfAny' ,sortable: false},
         { text: 'IsRequired?',align:'center', value: 'IsRequired?',sortable: false},
         { text: 'Url',align:'center', value: 'Url' },
         { text: 'AbsoluteXpath',align:'left', value: 'AbsoluteXpath',sortable: false}
       ],
       classval:'start',
       srcval:'/img/starticon.png',
       items:[],
       // items:[{TagName:'1',pagename:'2',ObjectName:'3',RelativeXpath:'4',AbsoluteXpath: ['5','6'],CssSelector:'7',Action:'8',ValueIfAny:'9',IsRequired:'10',Url:'11'},
                // {TagName:'11',pagename:'12',ObjectName:'13',RelativeXpath:'14',AbsoluteXpath: ['15','16'],CssSelector:'17',Action:'18',ValueIfAny:'19',IsRequired:'20',Url:'21'},
                // {TagName:'21',pagename:'22',ObjectName:'23',RelativeXpath:'24',AbsoluteXpath: ['25','26'],CssSelector:'27',Action:'28',ValueIfAny:'29',IsRequired:'30',Url:'31'}],
       selected:[],
       search:'',
       pagination:{
         rowsPerPage:'13'
       },
       dialog: false,
       filename:'',
       dialog1:false,
       selectedfile:'',
       dialog2:false,
       temprecord:[]
      }
    },
    computed:{
            computeditems:function(){
                return this.items;
             },
             pages () {
                return this.pagination.rowsPerPage ? Math.ceil(this.items.totalItems / this.pagination.rowsPerPage) : 0
             },
             converteddata:function(){
               return JSON.stringify(this.items);
             }
    },
    methods:{
      fetchsteps:function(event){
                      var vueapp=this;
                      chrome.runtime.sendMessage({from: 'popup', subject: 'getdata'}, function(response) {
                                console.log(response);
                                vueapp.items=response;
                          });
        },
       enabledisable:function(event){
          chrome.runtime.sendMessage({
              from: 'popup',
              subject: 'enable/disable'
          }, function(response) {
              console.log(response.statusofrecorder);
          });
          if(event.target.className!='start'){
            this.srcval='/img/starticon.png';
            this.classval='start';
          }else{
          this.srcval='/img/pauseicon.png';
          this.classval='pause';
          }
      },
      resetdata:function(event){
              var vueapp=this;
             vueapp.items=[];//clear items in this page
             //disablefunction has to be triggered
             if(vueapp.classval=='pause'){
               vueapp.srcval='/img/starticon.png';
               vueapp.classval='start';
             }
             //send message to background to record false,clear array there too and set index back to 0
             chrome.runtime.sendMessage({
                 from: 'popup',
                 subject: 'reset'
             }, function(response) {
                 console.log(response.statusofrecorder);
             });
      },
      deleteselected:function(event){
              var vueapp=this;
              vueapp.selected.forEach((r) => {
                  const idx = vueapp.items.indexOf(r);
                  vueapp.items.splice(idx, 1);
              })
      },
      exportvalues:function(event){
              var vueapp=this;
              vueapp.dialog=false;//resetting the dialogbox
              // console.log(vueapp.filename);
              var dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(vueapp.converteddata);
              var downloadAnchorNode = document.createElement('a');
              downloadAnchorNode.setAttribute("href",dataStr);
              downloadAnchorNode.setAttribute("download", vueapp.filename + ".json");
              downloadAnchorNode.click();
              downloadAnchorNode.remove();
      },
      importsteps:function(event){
              var vueapp=this;
              vueapp.dialog1=false;//toclose prompt
              //reading from file . First get the selected file path code pending
              var reader = new FileReader();
              reader.onload = function(event) {
                  var contents = event.target.result;
                  console.log("File contents: " + contents);
                  vueapp.items=JSON.parse(contents);
              };
              reader.onerror = function(event) {
                  console.error("File could not be read! Code " + event.target.error.code);
              };
              reader.readAsText(vueapp.selectedfile);
      },
      setselectioninlocal:function(event){
              var vueapp=this;
              vueapp.selectedfile=event.target.files[0];
      },
      insertrecord:function(event){
              var vueapp=this;
              //on hitting save this inserrecord has to work now.
              var objtopush={TagName:vueapp.temprecord.TagName,pagename:vueapp.temprecord.Pagename,
                              ObjectName:vueapp.temprecord.Objectname,RelativeXpath:vueapp.temprecord.RelativeXpath,
                              AbsoluteXpath: [vueapp.temprecord.Absolutexpath],CssSelector:'',Action:vueapp.temprecord.Selecteditemineditor,
                              ValueIfAny:vueapp.temprecord.ValueIfAny,IsRequired:'Y',Url:vueapp.temprecord.Url};
              vueapp.items.splice(vueapp.temprecord.rowindex-1,0,objtopush);//insert in items array
              vueapp.dialog2=false;//resetting the dialogbox
              //vueapp.temprecord=[];//clear the array as needed but commenting as may be useful to user not to rewrite it everytime
      }
    }
    }
</script>



